body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --h1-size: 3.2rem;
  --h1-weight: 500;
  --h2-size: 2.4rem;
  --h3-size: 2rem;
  --h3-weight: 400;
  --p-size: 1.2rem;
  --p-lineheight: 1.8rem;
  color: var(--paragraph-text);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Tablet Breakpoint */
@media (max-width: 1200px) {
  body {
    --h1-size: 2.8rem;
    --h1-weight: 400;
    --h2-size: 2rem;
    --h3-size: 1.6rem;
    --h3-weight: 300;
    --p-size: 1.1rem;
    --p-lineheight: 1.5em;
  }
}

/* Phone Breakpoint*/
@media (max-width: 600px) {
  body {
    --h1-size: 2rem;
    --h1-weight: 400;
    --h2-size: 1.8rem;
    --h3-size: 1.4rem;
    --h3-weight: 300;
    --p-size: 1rem;
    --p-lineheight: 1.4rem;
  }
}
